/* Misc visuals
----------------------------------*/

/* Corner radius */
.ui-corner-all,
.ui-corner-top,
.ui-corner-left,
.ui-corner-tl {
	border-top-left-radius: 3px;
}
.ui-corner-all,
.ui-corner-top,
.ui-corner-right,
.ui-corner-tr {
	border-top-right-radius: 3px;
}
.ui-corner-all,
.ui-corner-bottom,
.ui-corner-left,
.ui-corner-bl {
	border-bottom-left-radius: 3px;
}
.ui-corner-all,
.ui-corner-bottom,
.ui-corner-right,
.ui-corner-br {
	border-bottom-right-radius: 3px;
}

/* Overlays */
.ui-widget-overlay {
	background: #aaaaaa;
	opacity: .3;
	filter: Alpha(Opacity=30); /* support: IE8 */
}
.ui-widget-shadow {
	-webkit-box-shadow: 0px 0px 5px #666666;
	box-shadow: 0px 0px 5px #666666;
}

.price-range-block {
  margin: 60px;
}

.sliderText {
  width: 40%;
  margin-bottom: 30px;
  border-bottom: 2px solid red;
  padding: 10px 0 10px 0px;
  font-weight: bold;
}

.ui-slider-horizontal {
  height: 4px;
  position: relative;
  margin: 40px 15px 10px;
}
.ui-slider-horizontal {
  margin-bottom: 15px;
  width: 90%;
  margin-inline: auto;
}
.ui-widget-header {
  background: #000;
}

.ui-widget.ui-widget-content {
  background-color: #e6e6e6;
  border: none;
}

.ui-state-default,
.ui-widget-content .ui-state-default,
.ui-widget-header .ui-state-default,
.ui-button,
html .ui-button.ui-state-disabled:hover,
html .ui-button.ui-state-disabled:active {
  border-radius: 50%;
  background-color: white;
  border-color: black;
}

.price-range-search {
  width: 40.5%;
  background-color: #f9f9f9;
  border: 1px solid #6e6666;
  min-width: 40%;
  display: inline-block;
  height: 32px;
  border-radius: 5px;
  float: left;
  margin-bottom: 20px;
  font-size: 16px;
}
.price-range-field {
  width: 20%;
  min-width: 16%;
  background-color: #f9f9f9;
  border: 1px solid #6e6666;
  color: black;
  font-family: myFont;
  font: normal 14px Arial, Helvetica, sans-serif;
  border-radius: 5px;
  /* height: 26px; */
  padding: 5px;
}
.search-results-block {
  position: relative;
  display: block;
  clear: both;
}


